import Vue from 'vue'
import Router from 'vue-router'
import Come from 'components/come'
import Home from 'components/home'
import Rank from 'components/rank'
import New from 'components/new'
import Search from 'components/search'
import List from 'components/list'
import Singer from 'components/singer'
import ServerAgrement from 'components/tool/ServerAgrement'
import Uninstall from 'components/tool/Uninstall'
import fastclick from 'fastclick'

fastclick.attach(document.body)

Vue.use(Router)

export default new Router({
  linkActiveClass: 'active',
  routes: [
    {
      path: '/',
      redirect:'/come'
    },
    {
      path: '/home',
      component:Home,
      children:[
        {
          path:'/',
          component:New
        },
        {
          path:'/new',
          component:New
        },
        {
          path:'/rank',
          component:Rank
        },
        {
          path:'/list',
          component:List
        },
        {
          path:'/singer',
          component:Singer
        }
      ]
    },
    {
      path: '/come',
      component:Come
    },
    {
      path: '/server',
      component:ServerAgrement
    },
    {
      path: '/uninstall',
      component:Uninstall
    },
  ]
})



