<template>
  <swiper :options="options" ref="mySwiper" class="swiper">
    <swiper-slide v-for="(item,index) in items" class="swiper-slide" :class="item.class" :key="index">
      <img v-for="img in item.imgs" :src="img.src" :class="img.class" :key="img.src"/>
      <a @click="jump(link.class)" v-for="link in item.links" :key="link.src" :href="link.href" :class="link.class">
        <img :src="link.src" />
      </a>
      <div v-if="index!=4" class="button-next" @click="slideNext" ref="button"></div>
      <end-page @come="slideToFirst" v-show="index===4"></end-page>
    </swiper-slide>

</swiper>

</template>
<script>
import 'swiper/dist/css/swiper.css'
import {swiper,swiperSlide} from "vue-awesome-swiper"
import EndPage from "components/come/Endpage"
import {testCookie,setCookie} from "@/store/cookie.js"
export default {
  name: 'come',
  components:{
    swiper,swiperSlide,EndPage
  },
  created(){
    if(testCookie("new")){
      // this.$router.push('/home')
    }else{
      setCookie("new","new")
    }
  },
  computed:{
    swiper(){
      return this.$refs.mySwiper.swiper;
  },
  items(){return[
          {
            class:{p1:true,active:this.activeIndex==0},
            imgs:[
                    {src:"./static/p1-1.png",class:"title"},
                    {src:"./static/p1-4.png",class:"main"},
                    {src:"./static/p1-2.jpg",class:"bottom-bg"}
            ],
            links:[
              {src:"./static/p1-3.png",href:"https://itunes.apple.com/app/apple-store/id472208016?pt=624347&ct=%E9%85%B7%E7%8B%97mo&mt=8",class:"download"},
              {src:"./static/p1-5.png",href:"itms-services://?action=download-manifest&url=https://mo.kugou.com/download/html/kugou0.plist",class:"yydownload"},
            ]
          },
          {
            class:{p2:true,active:this.activeIndex==1},
            imgs:[
              {src:"./static/p2-1.png",class:"title"},
              {src:"./static/p2-2.png",class:"main"},
            ]
          },
          {
            class:{p3:true,active:this.activeIndex==2},
            imgs:[
                    {src:"./static/p3-1.png",class:"title"},
                    {src:"./static/p3-2.png",class:"main"}
            ]
        },
        {
          class:{p4:true,active:this.activeIndex==3},
          imgs:[
            {src:"./static/p4-1.png",class:"title"},
            {src:"./static/p4-2.png",class:"main"},
          ]
        },
        {
          class:{p5:true,active:this.activeIndex==4}
        }
      ]}
  },
  methods:{
    slideToFirst(){
      this.swiper.slideTo(0)
    },
    slideNext(){
      console.log(this.swiper)
      // this.swiper.slideTo(3, 1000, false)
      this.swiper.slideNext();
    },
    jump:function(name){
      if(name==="download"){
        _hmt.push(['_trackEvent', 'IOS', 'iosdownload', 'ioskugou']);
      }
      else if(name==="yydownload"){
        _hmt.push(['_trackEvent', 'IOSyy', 'iosyydownload', 'iosyykugou']);
      }
    }
  },
  data(){
    return{
        activeIndex:0,
        options:{
          autoplay:false,
          loop:false,
          notNextTick:true,
          roundLenghts:true,
          direction:"vertical",
          on:{
            slideChangeTransitionEnd:()=>{
              this.activeIndex=this.swiper.activeIndex;
            }
          },

        }
    }
  }
}
</script>

<style lang="less" type="text/less" scoped>
@import "~common/less/mixin";

@keyframes title{
  from{
    opacity:0;
    transform:scale(2);
  }
  to{
    opacity:1;
    transform:scale(1);
  }
}
@keyframes arrow{
  from,to{
    opacity:0;
  }
  50%{
    opacity:1;
    transform:translateY(-20px)
  }
}
@keyframes main{
  from{
    transform: translateY(500px);
    opacity: 0;
  }
  to{
    opacity: 1
  }
}
  .swiper{
    position:absolute;
    left:0;
    right:0;
    top:0;
    bottom:0;
    .swiper-slide{
      .bg-image;
      position: relative;
      img,a{
        position: absolute;
        opacity: 0

      }
      .title{
          width: 44.5%;
          top:12%;
          left:27.75%;
      }
      .main{
        width: 77%;
        top:28%;
        left:11.5%;
      }
      &.p1{
        background-image:url("~components/come/p1.jpg");
        .main{
          width: 60%;
          left: 20%;
          top:22%;
        }
        .bottom-bg{
          opacity:1;
          bottom:0;
          left:0;
          width:100%;
        }
        .img(){
          position: relative;
          width: 100%;
          opacity: 1;

        }
        .download{
          width: 38.6%;
          left: 8.4%;
          bottom: 6%;
          opacity: 1;
          img{
            .img;
          }


        }
        .yydownload{
          width: 38.6%;
          right: 8.4%;
          bottom: 6%;
          opacity: 1;
          img{
            .img;
          }

        }
      }
      &.p2{
        background-image:url("~components/come/p2.jpg")
      }
      &.p3{
        background-image:url("~components/come/p3.jpg")
      }
      &.p4{
        background-image:url("~components/come/p4.jpg")
      }
      &.p5{
        background-image:url("~components/come/p5.jpg")
      }
      .button-next{
          position:absolute;
          opacity: 0;
          top:95%;
          left:50%;
          margin-left:-18px;
          width:36px;
          height:26px;
          .bg-image;
          background-image:url("~components/come/next.png");
      }
      &.active {
        .button-next{
        animation:arrow 1s 1s linear both infinite;
        }
        .title{
          animation: title .5s 0s linear both;
        }
        .main{
          animation: main .5s .5s linear both;
        }


      }
    }


  }
</style>



